import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import Tabs from "react-responsive-tabs";
import { Table } from 'reactstrap';
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  ListGroup,
  ListGroupItem,
} from "reactstrap";

import { toast, Bounce } from "react-toastify";
import CountUp from "react-countup";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  faAngleUp,
  faAngleDown,
  faDotCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import ApiCall from "../../../Services/Function/ApiCall";
import Loading from '../../../Loading';
class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name:null,
      id:null,
      flag:false,
    };
  }
  HandleSetState=(key,value)=>
  {
    this.setState({
      [key]: value
    });
  }
  handleAdd=(event)=>
  {
    this.HandleSetState('name',event.target.value);
  }
  handleSubmit=async(e)=>
  {
    e.preventDefault();
    let data={
      'name':this.state.name
    }
    if(this.state.id)
    {
      data['id']=this.state.id;
    }
    let response=await ApiCall.postAPICall(1,'ticket/type',data);
    var type='success';
    var html='';
    if(response.code == 401)
    {
      type='error';
      Object.keys(response.error).map((val, i) => {
        html=html+response.error[val][0]+'\n';
      })
    }else
    {
      html=response.message;
    }
    (this.toastId = toast(
      html,
      {
        transition: Bounce,
        closeButton: true,
        autoClose: 2000,
        position: "bottom-center",
        type: type,
        bodyOutputType: 'trustedHtml'
      }

    ))
    this.HandleSetState('name',null);
    this.HandleSetState('id',null);
  }
  async componentDidMount() {
    this.HandleSetState('flag',true);
    if(localStorage.getItem('ticket_type_edit'))
    {
      let edit_data=JSON.parse(localStorage.getItem('ticket_type_edit'));
      this.HandleSetState('id',edit_data.id);
      this.HandleSetState('name',edit_data.name);
    }
  }
  HandleDisplay=()=>
  {
    return (
      <Row className="d-flex justify-content-center">
         <form className="col-md-12 ">
            <div className="form-group">
              <label >Type</label>
              <input type="text" value={this.state.name} onChange={this.handleAdd} className="form-control" placeholder="Enter Type"/>
              <small className="form-text text-muted"></small>
            </div>
            <button type="submit" className="btn btn-primary" onClick={(e) => this.handleSubmit(e)}>Submit</button>
          </form>
      </Row>);
  }
  render() {
    return (
      <Fragment>
        {this.state.flag ? 
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
          transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
          <Container fluid>
            {this.HandleDisplay()}
          </Container>
        </CSSTransitionGroup>
        :
          <Loading/>
        }
      </Fragment>
    );
  }
}

export default index;
