import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import { Table } from 'reactstrap';
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  ListGroup,
  ListGroupItem,
} from "reactstrap";
import ReactTable from "react-table";
import CountUp from "react-countup";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  faAngleUp,
  faAngleDown,
  faDotCircle,
} from "@fortawesome/free-solid-svg-icons";
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import ApiCall from "../../../Services/Function/ApiCall";
import Loading from '../../../Loading';

import { toast, Bounce } from "react-toastify";
class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data:[],
      key:1,
      is_update:false,
      searchInput:null,
      statusInput:null,
      ticket_status:{},
      flag:false,
      filteredData:null,
    };
  }
  handleAddButton=()=>{
    localStorage.setItem('ticket_edit',[]);
    let url =window.location.href+'/add';
    window.location.href=url;
  }
  HandleSetState=(key,value)=>
  {
    this.setState({
      [key]: value
    });
  }
  getdata=async()=>
  {
    let response_status=await ApiCall.getAPICall(1,'ticket/status');
    this.HandleSetState('ticket_status',response_status.data);
    var user=JSON.parse(localStorage.getItem('user'));
    console.log(user);
    let obj={
      'user_id':user['Id'],
    }
    let response=await ApiCall.postAPICall(1,'ticket/issue/list',obj);
    this.HandleSetState('data',response.data); 
    this.HandleSetState('filteredData',response.data); 
  }
  async componentDidMount() {
    await this.getdata();
    this.HandleSetState('flag',true);
  }
  componentDidUpdate(prevProps, prevState) {
    if (!this.state.is_update && this.state.filteredData && this.state.key !== prevProps.data) { 
      this.HandleSetState('is_update',true);
      this.HandleDisplayData(this.state.key);
    }
  }
  handleDeleteButton=async(id)=>
  {
    let url='ticket/issue/delete';
    let obj={
      'id':id
    }
    let response=await ApiCall.postAPICall(1,url,obj);
    var type='success';
    var html='';
    if(response.code == 401)
    {
      type='error';
      Object.keys(response.error).map((val, i) => {
        html=html+response.error[val][0]+'</br>';
      })
    }else
    {
      html=response.message;
      this.getdata();
    }
    (this.toastId = toast(
      html,
      {
        transition: Bounce,
        closeButton: true,
        autoClose: 2000,
        position: "bottom-center",
        type: type,
      }
    ))
    
  }
  handleEditButton=async(data)=>
  {
    localStorage.setItem('ticket_edit', JSON.stringify(data));
    var url =window.location.href+'/add';
    window.location.href=url;
  }
  globalSearch = () => {
    let { searchInput, data,key } = this.state;
    let filteredData = data.filter(value => {
    return (
        value.ticket_status_id.includes(key)
      );
    });
    filteredData = filteredData.filter(value => {
    return (value.description.toLowerCase().includes(searchInput.toLowerCase())
        || value.name.toLowerCase().includes(searchInput.toLowerCase())
        || value.email.toLowerCase().includes(searchInput.toLowerCase())
      );
    });
    this.setState({ filteredData: filteredData });
  };
  handleChange = event => {
    this.setState({ searchInput: event.target.value }, () => {
      this.globalSearch();
    });
  };
  setKey=(k)=>
  {
    this.setState({ key: k });
    this.HandleSetState('is_update',false);
  }
  HandleTabData=(filteredData)=>
  {
    this.setState({ filteredData: filteredData });
  }
  HandleDisplayData=(id)=>
  {
    let data = this.state.data;
    let filteredData = data.filter(value => {
    return (
        value.ticket_status_id.includes(id)
      );
    });
    this.HandleTabData(filteredData);
    // return filteredData;
  }
  HandleDisplay=()=>
  {
    const permission = this.props.permission;
    return (
      <Row className="d-flex justify-content-center">
         <div className="col-md-12">
         {this.props.permission['add']?
          <span className="pull-right" onClick={this.handleAddButton}>
            <Button  className="mb-2 mr-2" color="success">
            Add
            </Button>
          </span>
          :null}
          {this.props.permission['view']?
          <div className="col-md-4 pull-right">
            <input type="text"
              name="searchInput"
              onChange={this.handleChange}
              className="form-control" placeholder="Search"
            />
          </div>
          :null}
        </div>
        {this.props.permission['view']?
        <CardBody>
        <Tabs
              id="controlled-tab-example"
              activeKey={this.state.key}
              onSelect={(k) => this.setKey(k)}
              className="mb-3"
            >
            {typeof(this.state.ticket_status) !='undefined'?
              Object.keys(this.state.ticket_status).map((val, i) => (
                <Tab eventKey={this.state.ticket_status[i]['id']} title={this.state.ticket_status[i]['name'].toUpperCase()}>
                  <ReactTable data={this.state.filteredData}
                    columns={[
                      {
                        columns: [
                          {
                            Header: "Sr #",
                            accessor: "id",
                            headerStyle: {textAlign: 'left'},
                            Cell: (row) => {
                                return <div>{row.index+1}</div>;
                            }
                          }
                        ],
                      },
                      {
                        columns: [
                          {
                            Header: "Title",
                            accessor: "title",
                            headerStyle: {textAlign: 'left'}
                          }
                        ],
                      },
                      {
                        columns: [
                          {
                            Header: "Name",
                            accessor: "name",
                            headerStyle: {textAlign: 'left'}
                          }
                        ],
                      },
                      {
                        columns: [
                          {
                            Header: "Email",
                            accessor: "email",
                            headerStyle: {textAlign: 'left'}
                          }
                        ],
                      },
                      {
                        columns: [
                          {
                            Header: "Action",
                            accessor: "action",
                            headerStyle: {textAlign: 'left'},
                            Cell: (row) => (
                              <span>
                                {permission['edit']?
                                  <Button onClick={()=>this.handleEditButton(row.original)} className="mb-2 mr-2" color="primary">
                                    Edit
                                  </Button>
                                  :null}
                                  {permission['delete']?
                                  <Button onClick={()=>this.handleDeleteButton(row.original.id)} className="mb-2 mr-2" color="danger">
                                    Delete
                                  </Button>
                                  :null}
                              </span>
                            ),
                          },
                        ],
                      },
                    ]}
                    defaultPageSize={5}
                    className="-striped -highlight"
                  />
                </Tab>
              ))
              :null
            }
            </Tabs>
          </CardBody>
        :'Not Access'}
      </Row>);
  }
  render() {
    return (
      <Fragment>
        {this.state.flag ? 
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
          transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
          <Container fluid>
            {this.HandleDisplay()}
          </Container>
        </CSSTransitionGroup>
        :
          <Loading/>
        }
      </Fragment>
    );
  }
}

export default index;
