import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import Tabs from "react-responsive-tabs";
import { Table } from 'reactstrap';
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  ListGroup,
  ListGroupItem,
} from "reactstrap";

import { toast, Bounce } from "react-toastify";
import CountUp from "react-countup";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  faAngleUp,
  faAngleDown,
  faDotCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import ApiCall from "../../../Services/Function/ApiCall";
import Loading from '../../../Loading';
import {
  setPermissionData
} from '../../../../reducers/ThemeOptions';
import {connect} from 'react-redux';
class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name:null,
      permissionData:{},
      ticket_status:{},
      ticket_type:{},
      ticket_user:{},
      ticket_type_id:null,
      ticket_status_id:null,
      user_id:null,
      id:null,
      flag:false,
    };
  }
  HandleSetState=(key,value)=>
  {
    this.setState({
      [key]: value
    });
  }
  handleAddTitle=(event)=>
  {
    this.HandleSetState('title',event.target.value);
  }
  handleAddName=(event)=>
  {
    this.HandleSetState('name',event.target.value);
  }
  handleAddEmail=(event)=>
  {
    this.HandleSetState('email',event.target.value);
  }
  handleAddDescription=(event)=>
  {
    this.HandleSetState('description',event.target.value);
  }
  handleSubmit=async(e)=>
  {
    e.preventDefault();
    let data={
      'title':this.state.title,
      'name':this.state.name,
      'description':this.state.description,
    }
    if(this.state.id)
    {
      data['id']=this.state.id;
    }
    if(this.state.email)
    {
      data['email']=this.state.email;
    }
    if(this.state.ticket_type_id)
    {
      data['ticket_type_id']=this.state.ticket_type_id;
    }
    if(this.state.ticket_status_id)
    {
      data['ticket_status_id']=this.state.ticket_status_id;
    }
    if(this.state.user_id)
    {
      data['user_id']=this.state.user_id;
    }
    let response=await ApiCall.postAPICall(1,'ticket/issue',data);
    var type='success';
    var html='';
    if(response.code == 401)
    {
      type='error';
      Object.keys(response.error).map((val, i) => {
        html=html+response.error[val][0]+'\n';
      })
    }else
    {
      html=response.message;
    }
    (this.toastId = toast(
      html,
      {
        transition: Bounce,
        closeButton: true,
        autoClose: 2000,
        position: "bottom-center",
        type: type,
        bodyOutputType: 'trustedHtml'
      }

    ))
    this.HandleSetState('name',null);
    this.HandleSetState('id',null);
  }
  async componentDidMount() {
    let response_status=await ApiCall.getAPICall(1,'ticket/status');
    this.HandleSetState('ticket_status',response_status.data);
    let response_type=await ApiCall.getAPICall(1,'ticket/type');
    this.HandleSetState('ticket_type',response_type.data);  
    let response_user=await ApiCall.getAPICall(1,'user');
    this.HandleSetState('ticket_user',response_user.data); 
    this.HandleSetState('flag',true);
    if(localStorage.getItem('ticket_edit'))
    {
      let edit_data=JSON.parse(localStorage.getItem('ticket_edit'));
      this.HandleSetState('id',edit_data.id);
      this.HandleSetState('name',edit_data.name);
      this.HandleSetState('title',edit_data.title);
      this.HandleSetState('email',edit_data.email);
      this.HandleSetState('description',edit_data.description);
      this.HandleSetState('ticket_type_id',edit_data.ticket_type_id);
      this.HandleSetState('ticket_status_id',edit_data.ticket_status_id);
      this.HandleSetState('user_id',edit_data.user_id);
    }
    if(Object.keys(this.props.permissionData).length >0 && this.props.permissionData)
    {
      let permissionData=JSON.parse(this.props.permissionData);
      permissionData=permissionData['ticketassign'];
      this.HandleSetState('permissionData',permissionData);

    }
  }
  handleSelectOnChangeType=(id)=>
  {
    this.setState({
        'ticket_type_id': id
      });
  }
  handleSelectOnChangeStatus=(id)=>
  {
    this.setState({
        'ticket_status_id': id
      });
  }
  handleSelectOnChangeUser=(id)=>
  {
    this.setState({
        'user_id': id
      });
  }
  HandleDisplay=()=>
  {
    return (
      <Row className="d-flex justify-content-center">
         <form className="row col-md-12">
          <div className="row col-md-12">
            <div className="col-md-6 form-group">
              <label >Title</label>
              <input type="text" value={this.state.title} onChange={this.handleAddTitle} className="form-control" placeholder="Enter Title"/>
              <small className="form-text text-muted"></small>
            </div>
          </div>
            <div className="row col-md-12">
              {this.state.permissionData['ticket_type']?
              <div className="col-md-4 form-group">
                <label >Ticket Type</label>
                <select value={this.state.ticket_type_id} onChange = {(e)=> this.handleSelectOnChangeType(e.target.value)} className="form-control" name={'name'} >
                  <option value="">Select Ticket Type</option>
                  {typeof(this.state.ticket_type) !='undefined'?
                    Object.keys(this.state.ticket_type).map((val, i) => (
                      <option value={this.state.ticket_type[i]['id']}>{this.state.ticket_type[i]['name']}</option>
                    ))
                    :null
                  }
                </select>
              </div>
              :null}
              {this.state.permissionData['user']?
              <div className="col-md-4 form-group">
                <label >User</label>
                <select value={this.state.user_id} onChange = {(e)=> this.handleSelectOnChangeUser(e.target.value)} className="form-control" name={'name'} >
                  <option value="">Select User </option>
                  {typeof(this.state.ticket_user) !='undefined'?
                    Object.keys(this.state.ticket_user).map((val, i) => (
                      <option value={this.state.ticket_user[i]['Id']}>{this.state.ticket_user[i]['Name']}</option>
                    ))
                    :null
                  }
                </select>
              </div>
              :null}
              {this.state.permissionData['ticket_status']?
              <div className="col-md-4 form-group">
                <label >Ticket Status</label>
                <select value={this.state.ticket_status_id} onChange = {(e)=> this.handleSelectOnChangeStatus(e.target.value)} className="form-control" name={'name'} >
                  <option value="">Select Ticket Status</option>
                  {typeof(this.state.ticket_status) !='undefined'?
                    Object.keys(this.state.ticket_status).map((val, i) => (
                      <option value={this.state.ticket_status[i]['id']}>{this.state.ticket_status[i]['name']}</option>
                    ))
                    :null
                  }
                </select>
              </div>
              :null}
            </div>
            <div className="row col-md-12">
              <div className="col-md-12 form-group">
                <label >Description</label>
                <input type="text" value={this.state.description} onChange={this.handleAddDescription} className="form-control" placeholder="Enter Description"/>
                <small className="form-text text-muted"></small>
              </div>
            </div>
             <div className="row col-md-12">
              <div className="col-md-6 form-group">
                <label >Name</label>
                <input type="text" value={this.state.name} onChange={this.handleAddName} className="form-control" placeholder="Enter your Name"/>
                <small className="form-text text-muted"></small>
              </div>
              {this.state.permissionData['email']?
              <div className="col-md-6 form-group">
                <label >Email</label>
                <input type="text" value={this.state.email} onChange={this.handleAddEmail} className="form-control" placeholder="Enter Your Email"/>
                <small className="form-text text-muted"></small>
              </div>
              :null}
            </div>
            <div className="col-md-12">
              <button type="submit" className="btn btn-primary" onClick={(e) => this.handleSubmit(e)}>Submit</button>
            </div>
          </form>
      </Row>);
  }
  render() {
    return (
      <Fragment>
        {this.state.flag ? 
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
          transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
          <Container fluid>
            {this.HandleDisplay()}
          </Container>
        </CSSTransitionGroup>
        :
          <Loading/>
        }
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  permissionData:state.ThemeOptions.permissionData,
});

const mapDispatchToProps = dispatch => ({
  setPermissionData: enable => dispatch(setPermissionData(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(index);
