import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import Tabs from "react-responsive-tabs";
import { Table } from 'reactstrap';
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  ListGroup,
  ListGroupItem,
} from "reactstrap";
import ReactTable from "react-table";
import CountUp from "react-countup";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  faAngleUp,
  faAngleDown,
  faDotCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import ApiCall from "../../../Services/Function/ApiCall";
import Loading from '../../../Loading';

import { toast, Bounce } from "react-toastify";
class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data:[],
      flag:false,
      filteredData:null,
    };
  }
  handleAddButton=()=>{
    localStorage.setItem('ticket_status_edit',[]);
    let url =window.location.href+'/add';
    window.location.href=url;
  }
  HandleSetState=(key,value)=>
  {
    this.setState({
      [key]: value
    });
  }
  getdata=async()=>
  {
    let response=await ApiCall.getAPICall(1,'ticket/status');
    this.HandleSetState('data',response.data); 
    this.HandleSetState('filteredData',response.data); 
    
  }
  async componentDidMount() {
    await this.getdata();
    this.HandleSetState('flag',true);
  }
  handleDeleteButton=async(id)=>
  {
    let url='ticket/status/delete';
    let obj={
      'id':id
    }
    let response=await ApiCall.postAPICall(1,url,obj);
    var type='success';
    var html='';
    if(response.code == 401)
    {
      type='error';
      Object.keys(response.error).map((val, i) => {
        html=html+response.error[val][0]+'</br>';
      })
    }else
    {
      html=response.message;
      this.getdata();
    }
    (this.toastId = toast(
      html,
      {
        transition: Bounce,
        closeButton: true,
        autoClose: 2000,
        position: "bottom-center",
        type: type,
      }
    ))
    
  }
  handleEditButton=async(data)=>
  {
    localStorage.setItem('ticket_status_edit', JSON.stringify(data));
    var url =window.location.href+'/add';
    window.location.href=url;
  }
  globalSearch = () => {
    let { searchInput, data } = this.state;
    let filteredData = data.filter(value => {
    return (
        value.name.toLowerCase().includes(searchInput.toLowerCase())
      );
    });
    this.setState({ filteredData: filteredData });
  };
  handleChange = event => {
    this.setState({ searchInput: event.target.value }, () => {
      this.globalSearch();
    });
  };
  HandleDisplay=()=>
  {
    const permission = this.props.permission;
    return (
      <Row className="d-flex justify-content-center">
         <div className="col-md-12">
         {this.props.permission['add']?
          <span className="pull-right" onClick={this.handleAddButton}>
            <Button  className="mb-2 mr-2" color="success">
            Add
            </Button>
          </span>
          :null}
          {this.props.permission['view']?
          <div className="col-md-4 pull-right">
            <input type="text"
              name="searchInput"
              onChange={this.handleChange}
              className="form-control" placeholder="Search"
            />
          </div>
          :null}
        </div>
        {this.props.permission['view']?
         <CardBody>
          <ReactTable data={this.state.filteredData  ? this.state.filteredData : this.state.data}
            columns={[
              {
                columns: [
                  {
                    Header: "Sr #",
                    accessor: "id",
                    headerStyle: {textAlign: 'left'},
                    Cell: (row) => {
                        return <div>{row.index+1}</div>;
                    }
                  }
                ],
              },
              {
                columns: [
                  {
                    Header: "Type",
                    accessor: "name",
                    headerStyle: {textAlign: 'left'}
                  }
                ],
              },
              {
                columns: [
                  {
                    Header: "Action",
                    accessor: "action",
                    headerStyle: {textAlign: 'left'},
                    Cell: (row) => (
                        <span>
                          {permission['edit']?
                            <Button onClick={()=>this.handleEditButton(row.original)} className="mb-2 mr-2" color="primary">
                              Edit
                            </Button>
                            :null}
                            {permission['delete']?
                            <Button onClick={()=>this.handleDeleteButton(row.original.id)} className="mb-2 mr-2" color="danger">
                              Delete
                            </Button>
                            :null}
                        </span>
                              
                            ),
                  },
                ],
              },
            ]}
            defaultPageSize={5}
            className="-striped -highlight"
          />
        </CardBody>
        :'Not Access'}
      </Row>);
  }
  render() {
    console.log(this.props);
    return (
      <Fragment>
        {this.state.flag ? 
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
          transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
          <Container fluid>
            {this.HandleDisplay()}
          </Container>
        </CSSTransitionGroup>
        :
          <Loading/>
        }
      </Fragment>
    );
  }
}

export default index;
